@import "../../global.scss";

.projects {
  background-color: lightblue;
  flex-direction: column;
  align-items: center;
  margin-top: $topbarHeight;
  min-height: 100%;

  h1 {
    top: 100px;
    font-size: 40px;
    display: flex;
    justify-content: center;

    @include mobile {
      font-size: 30px;
    }
  }

  ul {
    margin: 10px;
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: center;

    @include mobile {
      flex-wrap: wrap;
    }
  }

  .container {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

    @include mobile {
      width: 100%;
    }
    .item {
      width: 220px;
      height: 150px;
      border-radius: 20px;
      border: 1px solid lightgray;
      margin: 10px 20px; //Top bot 10, left right 20
      display: flex;
      align-items: center;
      justify-content: center;
      color: black;
      position: relative;
      transition: all 0.5s ease;
      cursor: pointer;

      @include mobile {
        width: 130px;
        height: 100px;
      }

      h3 {
        position: absolute;
        font-size: 20px;
        color: white;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: 1;
      }

      &:hover {
        background-color: $mainColor;
        img {
          opacity: 0.2;
          z-index: 0;
        }
      }
    }
  }
}
